/* Responsive */
@media (max-width: 600px) {
  .new-game-container {
    font-size: 0.75em;
  }

  .win-game-container {
    font-size: 0.8em;
  }

  .game-tile {
    width: 25%;
    padding-bottom: 25%;
  }

  .easy .game-tile {
    width: 33.33%;
    padding-bottom: 33.33%;
  }

  @keyframes slide-left-skew {
    @include slide-left-animation(skewX(5deg));
  }
}
