/*
  Animation mixins
*/
@mixin bg-animate($color1, $color2, $color3) {
  0%, 100% { background: $color1; }
  33%  { background: $color2; }
  66% { background: $color3; }
}

@mixin opacity-toggle($startOpacity, $endOpacity) {
  50% {
    opacity: $startOpacity;
  }
  51%, 100% {
    opacity: $endOpacity;
  }
}

@mixin flip-animation($startRotation, $targetRotation) {
  0% {
    transform: perspective(400px) rotate3d(0, 1, 0, $startRotation + 0deg);
  }

  30% {
    transform: perspective(400px) rotate3d(0, 1, 0, $targetRotation - 90deg);
  }

  60%, 100% {
    transform: perspective(400px) rotate3d(0, 1, 0, $targetRotation + 0deg);
  }

  70% {
    transform: perspective(400px) rotate3d(0, 1, 0, $targetRotation + 20deg);
  }

  80 {
    transform: perspective(400px) rotate3d(0, 1, 0, $targetRotation - 10deg);
  }

  90% {
    transform: perspective(400px) rotate3d(0, 1, 0, $targetRotation + 10deg);
  }
}

@mixin slide-left-animation($transforms...) {
  0% {
    transform: translateX(150%) $transforms;
    opacity: 0.5;
  }
  50%{
    transform: translateX(-8%) $transforms;
  }
  65%{
    transform: translateX(4%) $transforms;
  }
  80%{
    transform: translateX(-4%) $transforms;
  }
  95%{
    transform: translateX(2%) $transforms;
  }
  100% {
    transform: translateX(0%) $transforms;
    opacity: 0.75;
  }
}

/*
  Background animations
*/
@keyframes bg-green-animate {
  @include bg-animate($bg-green, lighten($bg-green, 15%), darken($bg-green, 10%));
}

@keyframes bg-red-animate {
  @include bg-animate($bg-red, saturate($bg-red, 10%), darken($bg-red, 15%));
}

@keyframes bg-blue-animate {
  @include bg-animate($bg-blue, lighten($bg-blue, 15%), darken($bg-blue, 5%));
}

/*
  Makes the associated element look like it's floating
*/
@keyframes floating-middle {
  0%,100% {
    transform: rotate(-0.25deg) translate(-50%, -51%);
  }
  60% {
    transform: rotate(0.25deg) translate(-50%, -49%);
  }
}

/*
  Show/hide toggle at 50%
*/
@keyframes show {
  @include opacity-toggle(0, 1);
}

@keyframes hide {
  @include opacity-toggle(1, 0);
}

/*
  Flip animations
*/
@keyframes flip-in-y {
  @include flip-animation(0, 0);
}

@keyframes flip-out-y {
  @include flip-animation(0, 180);
}

/*
  Slide animations
*/
@keyframes slide-left-skew {
  @include slide-left-animation(skewX(20deg));
}

@keyframes slide-left {
  @include slide-left-animation();
}

@keyframes fade-in-then-out {
  0% {
    transform: scale(0);
    opacity: 0.0;
  }
  40% {
    transform: scale(1.1);
  }
  60% {
    transform: scale(0.9);
    opacity: 1;
  }
  95% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0.5);
    opacity: 0;
  }
}
