
/* Game board: the actual game itself */
.board-game-container {
  padding-top: 2em;
  background: $bg-purple;
  color: $fg-light;
  text-align: center;
}

.game-board {
  font-family: icon-font;
  padding: 2em;
}

.game-board:after {
  content: '';
  display: block;
  clear: both;
}

/*
The otermost element associated with a single game tile.
*/
.game-tile {
  font-size: 2em;
  position: relative;
  display: inline-block;
  border-right: 0;
  border-bottom: 0;
  box-sizing: border-box;
  width: 12.5%;
  float: left;

  /* Keep the aspect ratio */
  padding-bottom: 12.5%;
}

.easy .game-tile {
  width: 16.66%;
  padding-bottom: 16.66%;
}

/* Hide the checkbox */
.game-tile input {
  position: absolute;
  height: 0;
  width: 0;
  z-index: -2;
}

.game-card {
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  right: 1px;
  bottom: 1px;
  border-radius: 0.25em;
  background: $bg-med-purple;
}

.game-icon {
  @include centered();
}

/* Apply animation when the game card is not checked */
.game-tile input:not(:checked) + .game-card {
  animation: flip-out-y 0.5s forwards;
}

/* Hide the icon when the game card is not checked */
.game-tile input:not(:checked) + .game-card .game-icon {
  animation: hide 0.25s forwards;
}

/* Apply animation when the game card is checked */
.game-tile input:checked + .game-card {
  animation: flip-in-y 0.5s forwards;
}

/* Show the icon when the game card is checked */
.game-tile input:checked + .game-card .game-icon {
  opacity: 0;
  animation: show 0.5s forwards;
}

/* The bar that shows health, time, etc */
.game-details {
  text-align: justify;
}

/*
Force the text-align justify to apply:
it doesn't apply unless the line is long
enough to wrap.
*/
.game-details:after {
  content: '';
  display: inline-block;
  width: 100%;
}

.game-detail {
  display: inline-block;
  margin: 0 2em;
}

.game-detail-health {
  font-family: 'icon-font';
}
