
/* Win Screen */
.win-game-container {
  @include animated-game-container($bg-green, bg-green-animate);
}

.win-message {
  animation: fade-in-then-out 1.5s ease-in-out;
  opacity: 0;
  text-transform: uppercase;
}

.win-game-banner {
  position: absolute;
  color: $bg-green;
  background: $fg-light;
  opacity: 0.5;
  top: 0;
  bottom: 0;
  left: 5em;
  padding: 3em;

  opacity: 0;

  /* play the slide-left animation,
  take 0.5 seconds to run the animation
  use the ease-in-out algorithm
  wait 1.5 seconds before running the animation
  fowars: keep the last frame of the animation
  */
  animation: slide-left-skew 0.5s ease-in-out 1.5s forwards;
}

.game-stats {
  font-size: 2em;
  margin-bottom: 2em;
}

.game-stat {
  opacity: 0;
  animation: slide-left 0.25s ease-in-out 2s forwards;
}

.game-stat + .game-stat {
  animation-delay: 2.25s;
}

.game-stat + .game-stat + .game-stat {
  animation-delay: 2.50s;
}

.game-stat-score {
  font-size: 1.5em;
}
