/*
  Game-component mixins
*/
@mixin animated-game-container($bg-color, $animation-name, $fg-color: $fg-light) {
  background: $bg-color;
  color: $fg-color;
  text-align: center;

  animation: $animation-name linear 10s infinite;
}

@mixin centered() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/*
Game containers are the outer-wrapper around game content
*/
.game-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
}

/*
Game content is the inner-wrapper around the game content
*/
.game-content {
  @include centered();
}

/*
Reusable game components and base styles
*/
.main-header {
  font-family: 'Orbitron', sans-serif;
}

.ico {
  font-family: icon-font;
}

.menu-header {
  text-transform: uppercase;
}

.primary-button {
  display: inline-block;
  padding: 0.5em 1em;
  font-size: 2em;
  background: $bg-green;
  animation: bg-green-animate linear 2s infinite;
  color: $fg-light;
}

.menu-option {
  color: $fg-light;
  display: block;
  padding: 0.75em 2.5em;
  margin-bottom: 1.4em;
  border: 4px solid $fg-light;

  transform: perspective(200px) rotateY(15deg);
}

.menu-option:hover {
  background: $fg-light;
  color: $fg-dark;
  opacity: 0.8;
}

.menu-option:nth-child(even) {
  transform: perspective(200px) rotateY(-15deg);
}
